.section-container {
  margin-bottom: 5vh;
  height: 350px;
  display: flex;
  align-items: center;
}

.button {
  border: none;
  font-size: 2rem;
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: grid;
  z-index: 10;
  place-items: center;
  border-radius: 50%;
  background-color: white;
  color: black;
  position: absolute;
  text-decoration: none;
  box-shadow: 0 0 1em black;
}

.button svg{
  width: 15px;
}

.button:active {
  width: 48px;
  height: 48px;
}

.right {
  right: 2%;
}

.left {
  left: 2%;
}

.movie-section {
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  scroll-behavior: smooth;
  transition: 1s ease;
}

.movie-section::-webkit-scrollbar {
  width: 10px;
}

.movie-section::-webkit-scrollbar-track {
  background-color: #141414;
}

.movie-section::-webkit-scrollbar-thumb {
  background-color: #aaaa;
  border-radius: 10px;
}

.movie-section::-webkit-scrollbar-thumb:hover {
  background-color: #cacacaaa;
}

.titulo {
  margin-left: 10px;
}

@media screen and (max-width: 600px) {
  .movie-section {
    height: fit-content;
    width: 100vw;
    overflow-x: auto;
  }

  .button {
    display: none;
  }
}