@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
header {
  top: 0;
  position: fixed;
  z-index: 15;
  width: 100vw;
  padding: 0 80px;
  height: 8vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.titulo-header {
  text-decoration: none;
  color: red;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.search-header {
  display: flex;
}

.search-header button {
  cursor: pointer;
  border-radius: 5px;
  font-size: 1.1rem;
  border: none;
  color: white;
  background-color: red;
  padding: 5px 10px;
  margin-left: 10px;
}

.search-header button:active {
  font-size: 1rem;
}

@media screen and (max-width: 600px) {
  header {
    padding: 0 20px;
  }

  .search-header {
    display: none;
  }
}
.search-container {
  height: 35px;
  display: flex;
  justify-content: space-between;
}

.search-container select {
  color: white;
  background-color: #141414;
  margin-right: 10px;
  font-size: 1rem;
  border-radius: 5px;
}

.search-container input {
  background-color: #141414;
  border-radius: 3px;
  border: none;
  border-bottom: 1px solid white;
  color: white;
  padding: 10px;
  font-size: 1.2rem;
}
.firstMovie-section {
  align-items: center;
  width: 100vw;
  height: 70vh;
  display: flex;
  justify-content: space-between;
  background-size: cover;
  color: white;
  text-shadow: 2px 2px 5px black;
  font-size: 1.5rem;
  margin-top: 8vh;
  margin-bottom: 5vh;
}

.information-FirstMovie {
  width: 40%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 25px;
}

.information-FirstMovie h1 {
  align-self: center;
  font-size: 2.5em;
  margin-bottom: 3vh;
}

.overview {
  text-align: justify;
  margin-bottom: 5vh;
}

.information-FirstMovie h2 {
  margin-bottom: 5vh;
}

.rating {
  padding: 10px;
  border-radius: 10px;
  color: white;
  background-color: red;
}

.play-div {
  width: 40%;
  height: 100%;
  display: grid;
  place-items: center;
}

.play-button {
  border: none;
  width: 250px;
  height: 250px;
  font-size: 5rem;
  background-color: red;
  color: white;
  border-radius: 50%;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.play-button:hover {
  width: 255px;
  height: 255px;
  font-size: 5.1rem;
}

.play-button:active {
  width: 240px;
  height: 240px;
  font-size: 4.9rem;
}

@media screen and (max-width: 600px) {
  .play-button, .information-FirstMovie {
    display: none;
  }
}
.imagem-defalt {
  position: relative;
  cursor: pointer;
  flex-shrink: 0;
  height: 350px;
  width: 300px;
  background-size: 100% 100%;
  transition: .4s ease;
  box-shadow: 0 0 1em rgba(0, 0, 0, 0.2);
}

.imagem-hiden {
  color: white;
  position: fixed;
  width: 50vh;
  height: 70vh;
  z-index: 10;
  background-color: #141414;
  border-radius: 30px;
  display: none;
  justify-content: space-evenly;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  top: 10%;
  left: 35%;
}

.description {
  padding: 25px;
}

.imagem-hiden h1 {
  font-size: 2.5rem;
  margin-bottom: 3vh;
  margin: 15px;
}

.imagem-dentro-do-bgl {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
  padding: 25px;
}

.paragrafo {
  text-indent: 15px;
  overflow: auto;
  height: 40%;
  padding: 10px;
}

.paragrafo::-webkit-scrollbar {
  width: 3px;
}

.paragrafo::-webkit-scrollbar-track {
  background-color: #141414;
}

.paragrafo::-webkit-scrollbar-thumb {
  background-color: #aaaa;
  border-radius: 10px;
}

.paragrafo::-webkit-scrollbar-thumb:hover {
  background-color: #cacacaaa;
}

.close {
  align-items: flex-end;
  cursor: pointer;
  font-size: 3rem;
  font-weight: 900;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: white;
  text-shadow: 3px 3px 10px black;
}

.imagem-hiden img {
  height: 40%;
}

.rating {
  margin-bottom: 5vh;
}

@media screen and (max-width: 600px) {
  .imagem-hiden {
    display: flex;
    width: 100%;
    height: 80%;
    justify-content: space-between;
    left: 0;
  }
}
.section-container {
  margin-bottom: 5vh;
  height: 350px;
  display: flex;
  align-items: center;
}

.button {
  border: none;
  font-size: 2rem;
  cursor: pointer;
  width: 50px;
  height: 50px;
  display: grid;
  z-index: 10;
  place-items: center;
  border-radius: 50%;
  background-color: white;
  color: black;
  position: absolute;
  text-decoration: none;
  box-shadow: 0 0 1em black;
}

.button svg{
  width: 15px;
}

.button:active {
  width: 48px;
  height: 48px;
}

.right {
  right: 2%;
}

.left {
  left: 2%;
}

.movie-section {
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  scroll-behavior: smooth;
  transition: 1s ease;
}

.movie-section::-webkit-scrollbar {
  width: 10px;
}

.movie-section::-webkit-scrollbar-track {
  background-color: #141414;
}

.movie-section::-webkit-scrollbar-thumb {
  background-color: #aaaa;
  border-radius: 10px;
}

.movie-section::-webkit-scrollbar-thumb:hover {
  background-color: #cacacaaa;
}

.titulo {
  margin-left: 10px;
}

@media screen and (max-width: 600px) {
  .movie-section {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100vw;
    overflow-x: auto;
  }

  .button {
    display: none;
  }
}
.movies-sections-container-movielist {
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.bot-add-new-films {
  cursor: pointer;
  border: none;
  background-color: transparent;
  align-self: center;
  margin-bottom: 5vh;
}

.bot-add-new-films svg {
  color: white;
  width: 7em;
}

.bot-add-new-films svg:active {
  width: 6.8em;
}

h2 {
  margin: 10px;
  color: white;
}
.content-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.result-search-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  
  align-content: center;
  align-items: center;
  place-items: center;
  grid-gap: 20px;
  gap: 20px;
  margin-bottom: 5vh;
}

.titulo-busca {
  margin-left: 90px;
  margin-bottom: 15px;
  color: white;
  align-self: flex-start;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto, sans-serif;
}

body {
  width: 100vw;
  background-color: #141414;
  overflow-x: hidden;
}

body::-webkit-scrollbar {
  width: 15px;
}

body::-webkit-scrollbar-track {
  background-color: #141414;
}

body::-webkit-scrollbar-thumb {
  background-color: #aaaa;
  border-radius: 10px;
}

body::-webkit-scrollbar-thumb:hover {
  background-color: #cacacaaa;
}
