header {
  top: 0;
  position: fixed;
  z-index: 15;
  width: 100vw;
  padding: 0 80px;
  height: 8vh;
  background-color: black;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.titulo-header {
  text-decoration: none;
  color: red;
  user-select: none;
}

.search-header {
  display: flex;
}

.search-header button {
  cursor: pointer;
  border-radius: 5px;
  font-size: 1.1rem;
  border: none;
  color: white;
  background-color: red;
  padding: 5px 10px;
  margin-left: 10px;
}

.search-header button:active {
  font-size: 1rem;
}

@media screen and (max-width: 600px) {
  header {
    padding: 0 20px;
  }

  .search-header {
    display: none;
  }
}